.mindmap {
  width: 100vw;
  /* height: calc(100vh - 36px); */
  height: 100vh;
}

#root {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.hamburger-toggle div {
  position: relative;
  height: 100%;
}
.hamburger-toggle {
  position: relative;
  cursor: pointer;
  width: 48px;
  height: 48px;
  padding: 16px 14px;
}
@media screen and (max-width: 800px) {
  #menu-opener {
    display: inline-block !important;
    margin-left: auto;
  } 
}
#menu-opener {
    display: none;
}
.hamburger-toggle {
    position: relative;
    cursor: pointer;
    width: 48px;
    height: 48px;
    padding: 16px 14px;
}
@media screen and (max-width: 800px){
  .site-header .hamburger-toggle {
      margin-right: 14px;
  }
}
.hamburger-toggle span:first-child {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}
.hamburger-toggle span:nth-child(2) {
  position: absolute;
  top: 50%;
  left: 0;
  opacity: 1;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.hamburger-toggle span {
  background-color: #3D474D;
  width: 100%;
  border-radius: 1.125px;
  height: 2.25px;
  display: block;
  transition: all 0.3s ease-in-out;
}
.hamburger-toggle span:last-child {
  position: absolute;
  bottom: 0;
  left: 0;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}
.hamburger-toggle.open span:first-child {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: rotate(
  45deg
  );
    transform: rotate(
  45deg
  );
}
.hamburger-toggle.open span:nth-child(2) {
  position: absolute;
  top: 50%;
  left: 0;
  opacity: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.hamburger-toggle.open span:last-child {
  position: absolute;
  bottom: 0;
  left: 0;
  -webkit-transform: rotate(
  -45deg
  );
    transform: rotate(
  -45deg
  );
}
#responsive-menu {
  flex-grow: 1;
}
@media screen and (max-width: 800px){

  #responsive-menu {
    flex-direction: column;
    min-width: 100%;
    max-height: 0px;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
}
}
